section.contactUs {
  min-height: 80vh;
  padding-top: 4%;
}
section.contactUs .cotainer-fluid {
  max-width: 1133px;
  margin: 0 auto 50px;
}
section.contactUs input,
section.contactUs textarea {
  width: 100%;
  margin: 10px 0;
  padding: 10px 0;
  border: none;
  border-bottom: 1px solid #ccc;
}
textarea {
  height: 100px;
}
section.contactUs button {
  min-width: 150px;
}

input:focus,
textarea:focus {
  outline: 1px solid $Primay-color;
}
input:focus-visible,
textarea:focus-visible {
  outline: 1px solid $Primay-color;
}
