ul.navbar-nav.ml-auto.mb-2.mb-lg-0 {
  margin-left: auto;
}
a.navbar-brand {
  font-size: 20px;
  color: $Primay-color !important;
}
.nav-link,
.navbar-expand-lg .navbar-nav .nav-link {
  padding: 0;
  margin: 0 10px;
  color: #2f2e41 !important;
}
.menu_active {
  font-weight: 600;
  border-bottom: 2px solid $Primay-color;
}

.navbar_custom .container-fluid,
.navbar_custom .col-md-10 {
  padding: 0;
}
.navbar_custom .navbar-expand-lg {
  padding-top: 30px;
}
