@media screen and (max-width: 990px) {
  div#navbarNavDropdown {
    background: $Primay-color;
    padding: 30px;
    text-align: center;
    margin-top: 20px;
  }
  .nav-link,
  .navbar-expand-lg .navbar-nav .nav-link {
    padding: 0;
    margin: 0;
    font-size: 20px;
    color: #fff !important;
    text-transform: uppercase;
    display: inline-block;
    padding-bottom: 5px;
    margin-bottom: 16px;
  }
  .menu_active {
    font-weight: 600;
    border-bottom: 2px solid #fff;
  }
  section.common_banner .row .col-md-6:first-child,
  section.contactUs .row .col-md-6:first-child {
    order: 2;
    margin-top: 60px;
  }
  section.common_banner .row .col-md-6:last-child,
  section.contactUs .row .col-md-6:last-child {
    margin-top: 60px;
  }
  section.common_banner .cotainer-fluid,
  section.contactUs .cotainer-fluid {
    padding: 0 20px;
  }
  .h1,
  h1 {
    font-size: 25px;
  }
  section.common_banner .row .col-md-6:first-child h5.mt-3.mb-2 {
    font-size: 16px;
  }
  .btn_common {
    margin-bottom: 50px;
  }
  section.contactUs h1.mt-5.mb-5.text-center {
    margin: 0 !important;
    margin-bottom: 20px !important;
  }
  .error-heading {
    font-size: 30px;
  }
  .error_img {
    max-width: 200px;
  }
  section.contactUs .row .col-md-6 {
    order: 2;
  }
  section.contactUs .row .col-md-5 {
    margin: 50px 0;
  }
  .navbar_custom .col-10.mx-auto {
    width: 100%;
  }
  .navbar_custom .navbar-expand-lg {
    background: #6c63ff;
    padding: 20px 15px;
    margin-bottom: 30px;
  }
  a.navbar-brand {
    color: #ffffff !important;
  }
  .navbar-light .navbar-toggler-icon {
    filter: brightness(0) invert(1);
  }
  .navbar-light .navbar-toggler {
    border: none;
  }
  .navbar-light .navbar-toggler:focus-visible,
  .navbar-light .navbar-toggler:focus {
    outline: none !important;
    background: transparent;
  }
}
