.btn_common {
  color: #6c63ff;
  text-decoration: none;
  letter-spacing: 1px;
  font-size: 18px;
  border: 1px solid;
  border-radius: 50px;
  padding: 8px 30px;
  transition: all 0.3s ease-in-out;
  display: inline-block;
  margin-top: 20px;
}
.btn_common:hover {
  color: #fff;
  background: #6c63ff;
}
.btn-primary {
  background-color: $Primay-color;
  border-color: $Primay-color;
}
.btn-secondary {
  background-color: #2f2e41;
  border-color: #2f2e41;
}
